<template>
    <el-col :span="13">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="新闻标题" prop="title">
                <el-input v-model="ruleForm.title"></el-input>
            </el-form-item>
            <el-form-item label="新闻内容" prop="content">
                <quill-editor
                    ref="myTextEditor"
                    v-model="ruleForm.content"
                    :options="editorOption">
                </quill-editor>
            </el-form-item>
            <el-form-item label="是否显示">
                <el-radio-group v-model="ruleForm.isShow">
                    <el-radio class="radio" :label=0>是</el-radio>
                    <el-radio class="radio" :label=1>否</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">{{addTitle}}</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    import "quill/dist/quill.core.css";
    import "quill/dist/quill.snow.css";
    import "quill/dist/quill.bubble.css";
    import {quillEditor, Quill} from "vue-quill-editor";
    import {container, ImageExtend, QuillWatch} from "quill-image-extend-module";

    Quill.register("modules/ImageExtend", ImageExtend);
    export default {
        name: "addNews",
        mounted() {
            let self = this;
            let data = self.$route.query.data;
            if (data) {
                self.addTitle = "立即修改";
                self.ruleForm = data;
                self.method = "PUT";
            } else {
                self.method = "POST";
            }
        },
        components: {
            quillEditor
        },
        data() {
            return {
                gameData: [],
                addTitle: "立即添加",
                max: 3,
                ruleForm: {
                    title: "",
                    content: "",
                    isShow: 0
                },
                rules: {
                    title: [{
                        required: true, message: "请输入新闻标题", trigger: "blur"
                    }],
                    content: [{
                        required: true, message: "请输入新闻内容", trigger: "change"
                    }]
                },
                content: "",
                editorOption: {
                    modules: {
                        ImageExtend: {
                            loading: true, // 可选参数 是否显示上传进度和提示语
                            name: "uploadOss", // 图片参数名
                            size: 500, // 可选参数 图片大小，单位为M，1M = 1024kb
                            action: "http://test.bcrealm.com/api/wuser/oss/uploadFile", // 服务器地址, 如果action为空，则采用base64插入图片
                            // response 为一个函数用来获取服务器返回的具体图片地址
                            // 例如服务器返回{code: 200; data:{ url: 'baidu.com'}}
                            // 则 return res.data.url
                            response: res => {
                                return res.data;
                            },
                            headers: xhr => {
                            }, // 可选参数 设置请求头部
                            start: () => {
                            }, // 可选参数 自定义开始上传触发事件
                            end: () => {
                            }, // 可选参数 自定义上传结束触发的事件，无论成功或者失败
                            error: () => {
                            }, // 可选参数 自定义网络错误触发的事件
                            change: (xhr, formData) => {
                            } // 可选参数 选择图片触发，也可用来设置头部，但比headers多了一个参数，可设置formData
                        },
                        toolbar: {
                            container: container, // container为工具栏，此次引入了全部工具栏，也可自行配置
                            handlers: {
                                image: function () {
                                    // 劫持原来的图片点击按钮事件
                                    QuillWatch.emit(this.quill.id);
                                }
                            }
                        }
                    }
                }
            };
        },
        methods: {
            submitForm(formName) {
                var self = this;
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        self
                            .$ajax({
                                url: "wnews/admin/news/",
                                data: self.ruleForm,
                                method: self.method
                            })
                            .then(function (response) {
                                if (response.code === 1) {
                                    self.$notify({
                                        title: "成功",
                                        message: "新闻添加成功",
                                        type: "success",
                                        duration: 1000
                                    });
                                }
                            });
                    } else {
                        console.log("error submit!!");
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    };
</script>

<style lang="stylus">
</style>





